<template>
  <div class="box_week">
    <h2>&nbsp;월간 박스오피스 순위 Top10</h2>
    <ul>
      <li v-for="(wk,i) in 월간" :key="i">
        <img :src="월간이미지[i].image">
        <h4>{{wk.rank}}위</h4>
        <h3>{{wk.movieNm}}</h3>
        <p><strong>개봉일</strong> : {{wk.openDt}}</p>
        <p><strong>누적 관객수</strong> : {{numberComma(wk.audiAcc)}}명</p>
      </li>
    </ul>
  </div>
</template>

<script>
import mvweek from '../../mv_week.js';
import axios from 'axios';

export default {
    name: 'MyWeek',
    data(){
      return {
        월간이미지 : mvweek,
        월간: [],
      }
    },
    computed: {
      getAudiAcc() {
        return this.numberComma(this.월간.audiAcc);
      }
    },
    mounted() {
      this.getMomvieDataw()
    },
    methods : {
      getMomvieDataw: function(){
        let today = new Date();
        let year = today.getFullYear();
        let month = today.getMonth();
        let day = today.getDate()-1;
        let mvday = year+(("00"+month.toString()).slice(-2))+(("00"+day.toString()).slice(-2));
        console.log(month);
        axios.get('http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchWeeklyBoxOfficeList.json?key=6226ab394893d63ffdbcba33d6274f51&targetDt='+mvday+'&itemPerPage=10')        
            .then((b)=>{
                this.월간 = b.data.boxOfficeResult.weeklyBoxOfficeList
            })
            .catch((err)=>{err})
      },
      numberComma(num){
        return String(num).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
    },
}
</script>

