import { createWebHistory, createRouter } from "vue-router";
import Home from './components/template/Home.vue';
import Boxday from './components/template/Boxday.vue';
import Boxweek from './components/template/Boxweek.vue';
import Search from './components/template/Search.vue';

const routes = [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/Boxday",
    component: Boxday,
  },
  {
    path : "/Boxweek",
    component : Boxweek,
  },
  {
    path : "/Search",
    component : Search,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;