<template>
  <div class="mv_search">
    <h2>&nbsp;영화 제목 찾기</h2>
    <h5>찾으실 영화의 개봉년도를 입력하시거나 제목 일부를 아신다면 입력하세요</h5>
    <section>
      <input v-model="stt" placeholder="시작 년도"> ~ 
      <input v-model.number="end" placeholder="끝 년도">
      <input type="text" v-model="ntn" placeholder="영화제목 일부를 입력하세요">
      <button @click="getMomvieDatas()">검색</button>
    </section>

    <div>
      <article v-for="(sc,i) in 영화목록" :key="i">
        <h3>{{영화목록[i].movieNm}}</h3>
        <p>{{영화목록[i].prdtYear}}년</p>
        <p>{{영화목록[i].repNationNm}}</p>
      </article>
    </div>
    
    <!-- <input v-model.number="numy"> -->
    <!-- <h4>{{영화목록}}</h4> -->

  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'MySearch',
  data(){
    return {
      영화목록: [],
      조건1: "",
      stt : "",
      end : "",
      ntn : "",
    }
  },

  mounted() {
    this.getMomvieDatas()
  },

  methods : {
    getMomvieDatas: function(){
      var url = "http://www.kobis.or.kr/kobisopenapi/webservice/rest/movie/searchMovieList.json?key=6226ab394893d63ffdbcba33d6274f51&itemPerPage=20"
      
      // if(조건1 != "") url = url+ &조건1=우리가받은input

      if(this.end != "1900") url = url + "&prdtStartYear=" + this.stt + "&prdtEndYear=" + this.end + "&movieNm=" + this.ntn;
      console.log(url);

      // var formData = new FormData()
      // formData.append("key", "키값임")
      // axios.get(url).then()

      axios.get(url)
        .then((c)=>{
            this.영화목록 = c.data.movieListResult.movieList
        })
        .catch((err)=>{err})
    },

  },

  actions: {
    // getData(){
    //   axios.get().then((a)=>{
    //     a.data
    //   })
    // },
  },

  components: {
  },

}
</script>

