<template>

  <div class="box_day">
      <h2>&nbsp;실시간 예매순위</h2>
      <!-- <input type="date" v-model="selectedDate">
      <button @click="search">검색</button> -->
      <!-- <p>{{ $store.state.more }}</p> -->

      <ul>
        <li v-for="(mv,i) in 영화리스트" :key="i">
          <!-- <img :src="이미지[i].image"> -->
          <h4>예매순위 {{mv.rank}}위</h4>
          <div>
            <h3>{{mv.movieNm}}</h3>
            <p><strong>개봉일</strong> : {{mv.openDt}}&nbsp;&nbsp;|</p>
            <p><strong>예매율</strong> : {{mv.salesShare}}% &nbsp;|</p>
            <p><strong>금일 관객수</strong> : {{numberComma(mv.audiCnt)}}명</p>
          </div>
        </li>
      </ul>


      <!-- <ul>
        <li v-for="(mg,i) in 이미지" :key="i">
          <img :src="이미지[i].image">
        </li>
      </ul> -->

  </div>
</template>

<script>
import movie from '../../movie.js'
import axios from 'axios';

export default {
    name: 'MyDay',
    data(){
      return {
        이미지 : movie,
        영화리스트: [],
      }
    },
    computed: {
      getAudiAcc() {
        return this.numberComma(this.영화리스트.audiAcc);
      }
    },
    mounted() {
      this.getMomvieDatat()
    },
    methods : {
      getMomvieDatat: function(){
        let today = new Date();
        let year = today.getFullYear();
        let month = today.getMonth()+1;
        let day = today.getDate()-1;
        let mvday = year+(("00"+month.toString()).slice(-2))+(("00"+day.toString()).slice(-2));
        console.log(mvday);
        axios.get('http://www.kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchDailyBoxOfficeList.json?key=6226ab394893d63ffdbcba33d6274f51&targetDt='+mvday+'&itemPerPage=10')
            .then((a)=>{
                this.영화리스트 = a.data.boxOfficeResult.dailyBoxOfficeList
                console.log(this.영화리스트[0].movieNm);
            })
            .catch((err)=>{err})
      },
      numberComma(num){
        return String(num).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      }
    },
}
</script>

